.gallery {
    position: relative;
    margin-bottom: 87px;
    &__decoration_other {
        position: absolute;
        z-index: -1;
        left: -4%;
        bottom: -8% !important;
    }
    &__decoration {
        position: absolute;
        z-index: -1;
        left: -4%;
        bottom: 11%;
    }

    &__inner {
        position: relative;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    &__title {
        margin-bottom: 36px;
        text-align: center;
    }
    &__box {
        margin-bottom: 44px;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        margin-right: -20px;
        &-item {
            margin-right: 20px;
            max-width: 380px;
            width: 100%;
            height: 242px;
            box-shadow: 6px 6px 22px -9px rgba(58, 49, 38, 0.42);
            border-radius: 8px;
            margin-bottom: 22px;
            img {
                user-select: none;
                pointer-events: none;
            }
            cursor: zoom-in;
            &:nth-last-child(-n + 3) {
                margin-bottom: 0;
            }
            @media screen and (max-width: 820px) {
                &:nth-last-child(-n + 3) {
                    margin-bottom: 22px;
                }
            }
        }
        &-image {
            object-fit: contain;
            object-position: center;
        }
    }
    &__text {
        @include font(20px, 24px);
        text-align: center;
        margin-bottom: 33px;
    }
    &__button {
        font-size: 20px;
    }

    @media screen and (max-width: 835px) {
        &__box {
            justify-content: center !important;
            margin-right: 0;
            &-item {
              margin-right: 0;
            }
        }
    }
    @media screen and (max-width: 499px) {
        &__decoration {
            display: none;
        }
        &__text {
            @include font(18px, 22px);
            margin-bottom: 24px;
        }

        &__box-item {
            max-width: 320px;
            height: 204px;
            margin-right: 0;
            img {
                width: 320px;
                height: 204px;
            }
        }
    }
}
.gallery-examples {
    margin-top: 53px;
}
.gallery-bathroom {
    position: relative;
    margin-bottom: 107px;
    &__decoration {
        position: absolute;
        left: 15%;
        top: 4%;
        z-index: -1;
    }
}
.gallery-room,
.gallery-wardrob {
    margin-bottom: 117px;
}
.gallery-room,
.gallery-wardrob,
.gallery-bathroom {
    @media screen and (max-width: 1250px) {
        .gallery__box {
            justify-content: flex-start;
            &-item {
                margin-bottom: 24px;
                &:last-child {
                    margin-bottom: 0;
                }
            }
        }
    }
}
